/* btn */

.btn {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  line-height: 1.5rem;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 24px;
  padding-right: 24px;
  text-align: center;
  width: 100%;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
  font-family: Poppins;
}

@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}

@media (min-width: 1536px) {
  .btn {
    font-size: 1.34rem;
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 4rem;
    padding-right: 4rem;
  }
}

.btn--small {
